@import url(https://fonts.googleapis.com/css2?family=Stick+No+Bills:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bungee&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.toggle{
    height: 25px;
    width: 50px;
    border-radius:12.5px;
    position: relative;
    background-image: linear-gradient(aqua, skyblue);
    cursor: pointer;
}
.toggle:hover { 
    box-shadow: 0 0 2px rgba(33,33,33,1);
}
.toggle.night {
    background-image: linear-gradient(midnightblue,rebeccapurple);
    box-shadow: 0 0 3px whitesmoke;
}
.notch {
    height:25px;
    width: 25px;
    border-radius: 50%; 
    background: yellow;
    position: absolute;
    box-shadow: 0 0 1.25px yellow;
    transition: all 0.3s ease-in-out;
}
.night > .notch {
    background: whitesmoke;
    box-shadow: 0 0 1.25px whitesmoke;
    -webkit-transform: translate(25px, 0);
            transform: translate(25px, 0);
}

#headspace {
    display: flex;
    justify-content: space-between;
    margin: 1.5%;
    align-content: center;
    height: 100px;
    width: 95%;
    align-items: center;
    position: fixed;
}
#Logo {
    height: 125px;
    border-radius: .25em;
    margin: 25% 0 0 32.5%;
}
* {
    margin: 0px;
    padding: 0px;
}
#background {
    display: flex;
    flex-direction: column;
}
#foot_wrap {
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    width: 90%;
    bottom: 0;
    margin: 10px;
}
#footer_links {
    display: flex;
    justify-content: space-evenly;
    
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

